@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:"Quicksand", sans-serif}.btn{color:#fff;border:0;padding:10px 15px;border-radius:10px;width:100%}.btn-success{background-color:#28a745}.btn-dark{background-color:#343a40}.login{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-top:5%}.login form{border:1px solid black;border-radius:10px;padding:40px 10px;width:400px;text-align:center}.login form input{width:50%;margin:10px auto;display:block}.login form button{border:0;color:#fff;background:#343a40;padding:10px 20px}.home{display:-webkit-flex;display:flex;height:100vh}.home .sidebar{-webkit-flex:1 1;flex:1 1;background-color:#cecece}.home .sidebar-info{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:10px;margin-bottom:40px}.home .sidebar-info-image{margin-bottom:20px;border:2px solid #000;border-radius:50%;padding:20px}.home .sidebar-info p{font-size:24px}.home .sidebar-categories{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.home .sidebar-categories-category{width:150px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.home .sidebar-categories-category button{margin-left:10px;border-radius:10px;border:0;color:#000;background:transparent;font-size:16px}.home .main{overflow:scroll;-webkit-flex:4 1;flex:4 1;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.home .main .card{position:relative;overflow:auto;border:2px solid #000;border-radius:10px;margin:10px 20px;padding:20px 5px;-webkit-flex-basis:25%;flex-basis:25%;height:350px}.home .main .card:hover .card-saved-options{opacity:1}.home .main .card:hover .card-hide{opacity:0.4}.home .main .card-saved-options{z-index:2;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);opacity:0;display:-webkit-flex;display:flex}.home .main .card-saved-options button{margin-right:10px}.home .main .card-form{padding:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.home .main .card-form input,.home .main .card-form select{margin-bottom:20px;padding:5px}.home .main .card-form-new{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.home .main .card-form-new input{margin-bottom:0;width:80%}.home .main .card-form-new button{width:10%;padding:0}.home .main .card-todos{text-align:center}.home .main .card-todos-todo{font-size:16px;padding:10px 20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.home .main .card-todos-todo-completed{text-decoration:line-through;opacity:0.6}.home .main .card-todos-todo-buttons button{margin-left:10px;border-radius:10px;border:0;color:#000;background:transparent;font-size:16px}.home .main .card-add{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.home .main .card-add-title{font-size:32px;font-weight:600}.home .main .card-save{text-align:center;margin-top:50px}.home .main .card-delete{position:absolute;color:#000;top:0;right:0;padding:5px;font-size:16px;border-radius:50%}.home .main .card-delete:hover{color:#fff;background:#dc3545}@media screen and (max-width: 600px){.home{-webkit-flex-direction:column;flex-direction:column}.home .sidebar{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.home .sidebar-info{-webkit-flex-direction:row;flex-direction:row;margin-bottom:0}.home .sidebar-info-image{margin-right:10px}.home .main{-webkit-justify-content:center;justify-content:center}.home .main .card{-webkit-flex-basis:60%;flex-basis:60%}}

